import { Box, Text, Button } from '@chakra-ui/react'
import { Link } from "react-router-dom";

export const ErrorPage = () => {
    return (
        <Box w='100%' h='100vh' display="flex" alignItems="center" justifyContent='center' flexDirection='column'>
            <Text textAlign={'center'} mb={10} fontSize='3xl' fontWeight='black'></Text>
            <Link to="/Pokemon-TCG-Pokedex">
                <Button colorScheme='yellow' borderRadius='99em' fontSize='sm' color='black'></Button>
            </Link>
        </Box>
    );
}